





































import Vue from 'vue'

import * as DataAccess from '../assets/js/dataAccess'

export default Vue.extend({        
    name: "trials-table",
    components: {            
    },
    data() {
        return {
            search: '',
            yourDateString: '2018-12-24 04:19:23',
            slots: [
                'body',
                'body.append',
                'body.prepend',
                'footer',
                'header.data-table-select',
                'header',
                'header.<name>',
                'progress',
                'item.data-table-select',
                'item.<name>',
                'no-data',
                'no-results',
                'top'
            ],
            items: [],
        }
    },
 
    methods: {         
        getReports: function () {
            return [
                {
                    id: 1
                    , ship: "ship1"                      
                    , date: this.dateFormat(new Date(0))
                    , site: "site1"
                    , comments: "comments1"
                },
                {
                    id: 2
                    , ship: "ship2"
                    , date: this.dateFormat(new Date(0))
                    , site: "site2"
                    , comments: "comments2"
                },
                {
                    id: 3
                    , ship: "ship3"
                    , date: this.dateFormat(new Date(0))
                    , site: "site3"
                    , comments: "comments3"
                },                   
                {
                    id: 4
                    , ship: "ship4"
                    , date: this.dateFormat(new Date(0))
                    , site: "site4"
                    , comments: "comments4"
                },
                {
                    id: 5
                    , ship: "ship5"
                    , date: this.dateFormat(new Date(0))
                    , site: "site5"
                    , comments: "comments5"
                },
            ];
        },
        dateFormat: function (e){
               return new Intl.DateTimeFormat('en-US', {
                    year: 'numeric',
                    month: 'numeric',
                    day: 'numeric',
                    hour: 'numeric',
                    minute: 'numeric',
                    second: 'numeric',
                    }).format(e);
        },

        row_onClick: function (e) {
            console.log(e, " was clicked");
            (this as any).$vToastify.success("easy-peasy");
        },
      
    },
    computed: {
        root: function () {
            return null;// (<HTMLInputElement>document.getElementById('App-base-url')).value;                
        },
        headers: function () {
            return [{
                text: "Ship",
                value: "ship",
                "class": "ship"
            }, {
                text: "Date",
                value: "date",
                "class": "report-date"
            }, {
                text: "Site",
                value: "site",
                "class": "site"
            }, {
                text: "Comments",
                value: "comments",
                "class": "comments",
                sort: function (a, b) {
                    return a - b;
                }
            },
            ];
        },
        itemsDisplay: function () {
            const self = (this as any);
            if (self.items.sort) {
                return self.items.sort((a: any, b: any) => {
                    return a - b;
                });
            } else {
                return [];
            }
           
        }
    },
    mounted: function () {
        (this as any).items = this.getReports();
    },


})

